.container {
  display: flex;
}
.contactus {
  position: relative;
  top: 100px;
  left: 20px;
}
.content {
    display: flex;
    margin: 82px auto 32px;
    padding: 0 16px;
    flex-direction: column;
    align-items: center;
  }
  
  .card-main-container {
    display: flex;
    width: 100%;
    flex-flow: wrap;
  }
  
  .card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 16px;
  }
  
  .card {
    border-radius: 4px;
    border: 1px solid #eee;
    background-color: #fafafa;
    height: 200px;
    width: 200px;
    margin: 0 8px 16px;
    padding: 16px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    line-height: 24px;
  
    .prod-img {
      height: 160px;
      width: 160px;
    }
  }
  
  .card-container .card:not(:last-child) {
    margin-right: 0;
  }
  
  .card-container .card:not(.highlight-card) {
    cursor: pointer;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .card-container .card:not(.highlight-card):hover {
    transform: translateY(-3px);
    box-shadow: 0 4px 17px rgba(0, 0, 0, 0.35);
  }
  
  .card-container .card:not(.highlight-card):hover .material-icons path {
    fill: rgb(105, 103, 103);
  }
  
  
  a,
  a:visited,
  a:hover {
    color: #1976d2;
    text-decoration: none;
  }
  
  a:hover {
    color: #125699;
  }
  
  /* Responsive Styles */
  @media screen and (max-width: 767px) {
  
    .card-container>*:not(.circle-link),
    .terminal {
      width: 100%;
    }
  
    .card:not(.highlight-card) {
      height: 16px;
      margin: 8px 0;
    }
  
    .card.highlight-card span {
      margin-left: 72px;
    }
  
  }
  